.basketItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 2px solid #F4F4F4;
    padding: 30px 0;
    img {
        width: 80px;
        height: 80px;
        margin-right: 15px;
    }
}

.basketItem-pizza {
    display: flex;
    align-items: center;
    margin-right: 50px;
    width: 340px;
}

.basketItem-info {
    
}

.basketItem-title {
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
    color: #000000;
    margin-bottom: 3px;
}

.basketItem-text {
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.01em;
    color: #8D8D8D;
}

.basketItem-change {
    display: flex;
}

.btn-change-count {
    border: 2px solid #FE5F1E;
}

.count-pizza {
    margin: 0 12px;
}

.price-pizza {
    margin: 0 50px;
}

.count-pizza,
.price-pizza {
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
}

.btn-delete-basketItem {
    border: 2px solid #D7D7D7;
    transition: all .3s ease-in-out;

    &:hover {
        background-color: #000000;
        border: none;
        svg {
            path {
                fill: #FFFFFF;
            }
        }
    }
    svg,
    path {
        transition: all .2s ease-in-out;
    }
}

.btn-change-count,
.btn-delete-basketItem {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background: none;
}

.btn-plus,
.btn-minus {
    transition: all .3s ease-in-out;
    &:hover {
        background-color: #FE5F1E;
        svg {
            path {
                fill: #FFFFFF;
            }
        }
    }
}